import React from 'react';
import PropTypes from 'prop-types';
import { platform, IOS } from '@vkontakte/vkui';
import Panel from '@vkontakte/vkui/dist/components/Panel/Panel';
import PanelHeader from '@vkontakte/vkui/dist/components/PanelHeader/PanelHeader';
import PanelHeaderButton from '@vkontakte/vkui/dist/components/PanelHeaderButton/PanelHeaderButton';
import Icon28ChevronBack from '@vkontakte/icons/dist/28/chevron_back';
import Icon24Back from '@vkontakte/icons/dist/24/back';
import Div from '@vkontakte/vkui/dist/components/Div/Div';
import Button from '@vkontakte/vkui/dist/components/Button/Button';

import persik from '../img/persik.png';
import './RecordMenuStyle.css';

const osName = platform();

const RecordMenu = props => (
	<Panel id={props.id}>
		<PanelHeader
			left={<PanelHeaderButton onClick={props.go} data-to="mainMenu">
				{osName === IOS ? <Icon28ChevronBack/> : <Icon24Back/>}
			</PanelHeaderButton>}>Регистрация</PanelHeader>
		<Div className="MainRecord">
			<Div className= "TextBlockRecord">
				Выберите время, дату, и количество машинок
			</Div>
			<Div className= "RecordBlock"></Div>
			<Div className = "ButtonBlocksRecord">
				<Button className="ButtonBlcokRecord" size="xl" mode="outline">
					ЗАПИСАТЬСЯ
				</Button>
				<Button className="ButtonBlcokRecord" size="xl" mode="outline" onClick={props.go} data-to="mainMenu">
					Назад
				</Button>
			</Div>
		</Div>
	</Panel>
);

RecordMenu.propTypes = {
	id: PropTypes.string.isRequired,
	go: PropTypes.func.isRequired,
};

export default RecordMenu;
